<template>
  <div class="">
       <mt-header title="">
  <router-link to="/login" slot="left">
    <mt-button icon="back"></mt-button>
  </router-link> 
  </mt-header>
      <mt-field label="" placeholder="6-20" type="tel" v-model="password"></mt-field>
      <mt-button type="primary" size="large" @click="reset"></mt-button>
  </div>
</template>

<script>
export default {
  name: '',
  data() { 
    return {
     password:''
    }
  },
  methods:{
     reset(){
         
           this.$router.push({path:'/login'})
    
     }
    

  }
 }
</script>

<style lang="" scoped>
</style>


