<!--  -->
<template>
  <div>
    <div class="loginImg">
      <img src="../../../assets/logo.png" alt>
    </div>
    <mt-field label="" placeholder="/" v-model="username"></mt-field>

    <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
    <div class="btn-box">
      <mt-button type="primary" class="submit-btn" @click.native="toLogin"></mt-button>
    </div>
    <div class="footer-box">
      <div @click="forgetPassword"></div>
      <div @click="toRegister"></div>
    </div>
  </div>
</template>

<script>
import { requestPost } from "@/api/api.js";
import { setToken } from "@/utils/auth.js";
import { Toast } from "mint-ui";
import { hex_sha1 } from "@/utils/sha1.js";


export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },

  components: {},

  computed: {},

  methods: {
    toLogin() {
    //   requestPost("/api/v1/goods_list", {}).then(res => {
    //   //  this.goodsList=res.data
    //   if (res.status == "success" && res.data != []) {
    //     res.data.forEach(item => {});
    //   }
    //   alert(123)
    // });
      requestPost("/api/v1/user/mobile_login", {
        mobile: this.username,
        // password: hex_sha1(this.password) ,
        password: this.password ,

        mobile_prefix: "86"
      }).then(res => {
          
        if(res.data.status=="fail") {
          
          Toast({
            message: res.data.msg,
            position: "top",
            duration: 2000
          });
        }else if (res.data.status === "success") {
          Toast({
            message: "",
            position: "top",
            duration: 2000
          });
          localStorage.setItem("user_info", JSON.stringify(res.data.data));

          // console.log(typeof JSON.parse(localStorage.getItem("user_info")));
          setToken(res.data.data.token);

          this.$router.push({ path: "/", query: { status: "login" } });
        }
      })
    },
    forgetPassword() {
      // alert('change')
      this.$router.push({ path: "/forgetPassword" });
    },
    toRegister() {
      this.$router.push({ path: "/register" });
    }
  }
};
</script>
<style lang="less"  scoped>
.submit-btn {
  width: 95%;
  margin: 0 auto;
}
.btn-box {
  text-align: center;
  margin-top: 1rem;
}
.loginImg {
  width: 2.665245202558635rem;
  height: 2.665245202558635rem;
  border-radius: 100px;
  // border: 1px solid red;
  overflow: hidden;
  margin: 1rem auto;

  img {
    width: 100%;
    height: 100%;
  }
}
.footer-box {
  // position: fixed;
  width: 100%;
  bottom: 0;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  div {
    flex: 1;
  }
}
</style>


